
















import { Vue, Component, Prop } from 'vue-property-decorator'

@Component({
  name: 'VuiAvatar'
})
export default class VComponent extends Vue {
  @Prop({
    type: String,
    default: 'square'
  })
  type!: 'square' | 'circle'

  @Prop({
    type: String,
    default: 'regular'
  })
  size!: 'big' | 'regular' | 'small' | 'mini'

  @Prop({
    type: String,
    default: ''
  })
  src!: string

  @Prop({
    type: String,
    default: 'cover'
  })
  fit!: 'fill' | 'contain' | 'cover' | 'none' | 'scale-down'
}
